body {
  background-color: #e7ecf2;
  margin: 0;
  padding: 0;
}

ul,
ol,
h2,
p {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.container {
  width: 1200px;
  padding: 20px;
  margin: 0 auto;
}
